/* eslint-disable react/prop-types */
import React from 'react';
import { graphql } from 'gatsby';

import Layout from '../components/Layout/Layout';
import SEO from '../components/Layout/SEO';
import MessageScreen from '../components/MesssageScreen';

const normalizeSrcSets = ({
  data: { allFile: { nodes = [] } = {} } = {},
}) => nodes.reduce((obj, {
  childImageSharp: {
    fluid: { srcSet = null } = {},
  } = {},
  name,
  ...rest
} = {}) => ({
  ...obj,
  // [name]: srcSet,
  [name]: {
    ...rest,
    name,
    srcSet,
  },
}), {});

const IndexPage = (props) => (
  <Layout noHeader>
    <SEO title="METAKRONISMS" />

    <MessageScreen
      srcSets={normalizeSrcSets(props)}
      // eslint-disable-next-line react/destructuring-assignment
      description={props.data.site.siteMetadata.description}
    />
  </Layout>
);

export const query = graphql`
  {
    site {
      siteMetadata {
        title
        description
      }
    }
    allFile(filter: {extension: {in: ["png", "jpg", "jpeg"]}, dir: {regex: "/src\\/images$/"}}) {
      nodes {
        relativePath
        relativeDirectory
        root
        dir
        name
        extension
        childImageSharp {
          fluid {
            srcSet
          }
        }
      }
    }
  }
`;

export default IndexPage;
